import { useState } from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import Image34 from "../Atom/Image34";
import { uploadProductImage } from "../../services/cloudinary";
import { useRecoilValue } from "recoil";
import { currentUserAtom } from "../../store";
import FormItem from "../molecule/FormItem";
import { IProduct, categorys } from "../../@type";
import { writeDB } from "../../services/api";
import { useNavigate } from "react-router-dom";

interface IProductForm {
  brandName: string;
  name: string;
  description: string | undefined;
  category: string;
  originPrice: number;
  price: number;
  discount: number;
  options: string;
  images: FileList;
}

interface IImageFile {
  name: string;
  src: string;
}

const CreateProduct = () => {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm<IProductForm>();
  const [imageFiles, setImageFiles] = useState<IImageFile[]>([]);
  const currentUser = useRecoilValue(currentUserAtom);
  const originPrice = watch("originPrice");
  const discount = watch("discount");
  const navigate = useNavigate();

  const onUpload = (e: any) => {
    const files = e.target.files;
    if (imageFiles.length + files.length > 5) {
      alert("  5   .");
      return;
    }

    Array.from(files).map((file: any) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);

      return new Promise<void>((resolve) => {
        reader.onload = () => {
          setImageFiles((prev: any) => [...prev, { name: file.name, src: reader.result }]);
          resolve();
        };
      });
    });
  };

  const deleteImg = (idx: number) => {
    setImageFiles(imageFiles.filter((_: any, i: number) => i !== idx));
  };

  const onSubmit: SubmitHandler<IProductForm> = async (data) => {
    const confirm = window.confirm(" ?");
    if (!confirm) return;

    if (!currentUser) {
      alert(" .");
      return;
    }

    const productId = `${Date.now()}_${currentUser.uid}`;
    const price = Math.round((originPrice * (100 - discount)) / 100 / 10) * 10;
    const options = data.options.split(",");
    const images = await uploadProductImage(currentUser.uid, imageFiles);

    const save = {
      productId,
      ...data,
      price,
      images,
      options,
      originPrice: Number(originPrice),
      discount: Number(originPrice),
      sellerId: currentUser.uid,
      createAt: new Date(),
      updateAt: new Date(),
    } as IProduct;

    writeDB("/products", save);

    alert(" .");
    navigate("/");
  };

  return (
    <main className="max-w-5xl p-1 m-auto">
      <form
        className="max-w-xl m-auto p-1 mt-3 bg-[#f4f4f4] rounded shadow-md"
        onSubmit={handleSubmit(onSubmit)}
      >
        <h1 className="text-lg text-center py-5">  </h1>

        <div className="flex flex-col">
          <FormItem
            labelId="brandName"
            labelNm=""
            required
            element={
              <input
                type="text"
                placeholder=""
                className="text-sm indent-1 h-8 w-full"
                id="brandName"
                {...register("brandName", {
                  required: " .",
                  // validate: (value) => {
                  //   return !!value.trim();
                  // },
                  // pattern: {
                  //   value: /(^\s*)|(\s*$)/,
                  //   message: " ",
                  // },
                })}
                maxLength={10}
              />
            }
            error={errors.brandName?.message}
          />
          <FormItem
            labelId="name"
            labelNm=""
            required
            element={
              <input
                type="text"
                placeholder=""
                className="text-sm indent-1 h-8 w-full"
                id="name"
                {...register("name", { required: " ." })}
              />
            }
            error={errors.name?.message}
          />
          <FormItem
            labelId="description"
            labelNm=" "
            element={
              <textarea
                placeholder=" "
                className="text-sm indent-1 h-16 w-full"
                id="description"
                {...register("description")}
              />
            }
            error={errors.description?.message}
          />
          <FormItem
            labelId="category"
            labelNm=""
            required
            element={
              <select
                id="category"
                className="text-sm indent-1 h-8 w-full"
                {...register("category", { required: " ." })}
              >
                <option value=""></option>
                {categorys.map((category: any) => (
                  <option key={category.code} value={category.code}>
                    {category.label}
                  </option>
                ))}
              </select>
            }
            error={errors.category?.message}
          />
          <FormItem
            labelId="originPrice"
            labelNm=""
            required
            element={
              <input
                type="number"
                placeholder=""
                className="text-sm indent-1 h-8 w-full"
                id="originPrice"
                {...register("originPrice", { required: " ." })}
                min="0"
                step={10}
              />
            }
            error={errors.originPrice?.message}
          />
          <FormItem
            labelId="discount"
            labelNm=""
            required
            element={
              <input
                type="number"
                placeholder=" %"
                className="text-sm indent-1 h-8 w-full"
                id="discount"
                {...register("discount", {
                  required: " .  0 .",
                })}
                min="0"
                max="100"
                step={5}
              />
            }
            error={errors.discount?.message}
          />
          <FormItem
            labelId="price"
            labelNm=""
            element={
              <input
                type="number"
                className="text-sm indent-1 h-8 w-full bg-inherit"
                readOnly
                id="price"
                {...register("price")}
                value={`${Math.round((originPrice * (100 - discount)) / 100 / 10) * 10}`}
              />
            }
            error={errors.price?.message}
          />
          <FormItem
            labelId="options"
            labelNm=""
            required
            element={
              <input
                type="text"
                placeholder="(,)  "
                className="text-sm indent-1 h-8 w-full"
                id="options"
                {...register("options", { required: " 1  ." })}
              />
            }
            error={errors.options?.message}
          />
          <div className="mb-4 flex">
            <label className="w-24 text-sm">
              
              <span className="text-red-500">&nbsp;*</span>
            </label>
            <label
              htmlFor="images"
              className="text-sm text-center cursor-pointer p-1 rounded text-white bg-zinc-700 hover:bg-zinc-500 duration-100"
            >
               
            </label>
            <input
              type="file"
              className="hidden"
              accept="image/*"
              multiple
              id="images"
              {...register("images", { required: " ." })}
              onChange={(e) => onUpload(e)}
            />
            <p className="text-xs text-red-500 pt-1">{errors.images?.message}</p>
          </div>
          {imageFiles.length > 0 && (
            <>
              <div className="flex w-full h-40 border shadow-md overflow-x-auto bg-white p-3">
                {imageFiles.map((file: IImageFile, idx: number) => (
                  <div key={idx} className="relative w-20 mr-2 shrink-0">
                    <Image34 src={file.src} alt={file.name} />
                    <p className="text-xs truncate ...">{file.name}</p>
                    <button
                      className="absolute top-1 right-1 px-1 rounded text-xs text-white bg-zinc-700 hover:bg-zinc-500 duration-100"
                      onClick={() => deleteImg(idx)}
                    >
                      
                    </button>
                  </div>
                ))}
              </div>
              <p className="text-xs mb-1">   5  .</p>
              <p className="text-xs">  3:4  .</p>
            </>
          )}
        </div>

        <div className="flex justify-center pt-10 pb-5">
          <button className="bg-red-500 text-white py-2 px-8 rounded"> </button>
        </div>
      </form>
    </main>
  );
};

export default CreateProduct;
